import appClerkHistorys from '../../../components/page-component/app-clerk-historys/app-clerk-historys.vue';
import appFormData from "../../../components/basic-component/app-order/app-form-data.vue";

import { mapState } from "vuex";

export default {
    name: "detail",
    components: {
        appClerkHistorys,
        appFormData,
    },
    data() {
        return {
            haveForm: true,
            detail: {
                start_time: '',
                end_time: '',
            },
            card_id: null,
            checkPay: false,
            id: null,
            isShow: false,
        };
    },
    computed: {
        ...mapState({
            clerkImg: state => state.mallConfig.__wxapp_img.clerk,
            mall: state => state.mallConfig.mall
        }),
    },
    onLoad(options) { this.$commonLoad.onload(options);
        this.$showLoading({
            text: '加载中...'
        });
        if (options.id > 0) {
            this.id = options.id;
            uni.setNavigationBarTitle({
                title: "订单详情",
            })
        }else if (options.card_id > 0) {
            this.card_id = options.card_id;
            uni.setNavigationBarTitle({
                title: "卡券详情",
            })
        }
    },
    onShow: function() {
        if (this.id > 0) {
            this.getDetail();
        }else if (this.card_id > 0) {
            this.getCardDetail();
        }
    },
    methods: {
        showForm(e) {
            console.log(e)
            this.haveForm = e && e.length ? true : false
        },
        toClerk(id) {
            uni.navigateTo({
                url: '/pages/order/clerk/clerk?id=' + id
            });
        },
        toClerkCard(id) {
            uni.navigateTo({
                url: '/pages/card/clerk/clerk?cardId=' + id
            });
        },
        getDetail() {
            let that = this;
            that.$request({
                url: that.$api.clerk.detail,
                data: {
                    order_id: this.id
                },
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.detail = response.data.order;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getCardDetail() {
            let that = this;
            that.$request({
                url: that.$api.clerk.card_detail,
                data: {
                    cardId: this.card_id
                },
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.detail = response.data.card;
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        clerkAffirmPay() {
            let that = this;
            that.$request({
                url: that.$api.order.clerk_affirm_pay,
                data: {
                    id: that.id,
                    action_type: 1,
                },
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.checkPay = !that.checkPay;
                    that.getDetail();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        }
    }
}
